<template>
    <div>
      
      <!--  -->
      <div v-if="isLoading" class="loading-overlay">
        <img src="/jumpingsheep.gif" alt="..." class="loading-image"/>
      </div>
      <!--  -->
      <div v-if="isServerReturn==false" class="First-Part">
        <h3 class="title_description">
            (csv,xlsx)
        </h3>
  
        <!-- <input type="file" @change="handleCSV"> -->
        <input type="file" @change="handleFileChange">
        
      <div v-if="columns.length">
        <h3 class="title_description">
          2
        </h3>
        <!--  -->
        <table v-if="columns.length">
          <thead>
            <tr>
              <th>Select</th>
              <th>Column Name</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="column in columns" :key="column">
              <td><input type="checkbox" v-model="selectedColumns[column]"></td>
              <td>{{ column }}</td>
            </tr>
          </tbody>
        </table>
        <button class="submit-button" @click="handleSubmit" v-if="columns.length"></button>
      </div>
    </div>
  
      <!--  -->
      <div v-if="isServerReturn" class="Next-Part">
        <!--  -->
        <button class="loadData-button" @click="WatchData"></button>
        <button class="back-button" @click="GoBack"></button>
        <button v-if="isFetching==false" class="start-train-button" @click="toggleFetching">Train SBERT Model</button>
        <table v-if="isWatchData">
          <thead>
            <tr>
              <!-- DataFrame -->
              <th>Sentence1</th>
              <th>Sentence2</th>
              <!-- ...  ... -->
            </tr>
          </thead>
          <tbody>
            <tr v-for="row in dataTable" :key="row.id">
              <td>{{ row['sentence1'] }}</td>
              <td>{{ row['sentence2'] }}</td>
              <!-- ...  ... -->
            </tr>
          </tbody>
        </table>
      </div>
      <div v-if="isFetching | isTrained" class="trainlog">
          <h4>{{Flaskdata.data }}</h4>
      </div>
    </div>
  </template>
  
  <script>
  import Papa from 'papaparse';
  import * as XLSX from 'xlsx';
  import axios from 'axios';
  const web_api_url = 'http://127.0.0.1:5000';
  export default {
    data() {
      return {
        data: [],
        columns: [],          //  CSV 
        selectedColumns: {},  // 
        selectedColumnsArray:[],
        uploadedFile: null,  // 
        isLoading: false,  // false
        isWatchData:false,
        isServerReturn: false,
        dataTable: [],
        Flaskdata: {},
        isFetching:false,
        fetchInterval: null,  // ID
        isTrained: false,
      }
    },
    methods: {
        handleFileChange(event) {
        this.uploadedFile = event.target.files[0];
        if (this.uploadedFile.name.includes('csv')) {
          Papa.parse(this.uploadedFile, {
            complete: (results) => {
              this.data = results.data;
              // console.log("this.data", this.data); // assuming first row is header
              this.columns = results.data[0];
              // console.log("Column Names:", this.columns); // assuming first row is header
            }
          });
        }
        else if (this.uploadedFile.name.includes('xl')) {
          // console.log("file name", this.uploadedFile); // assuming first row is header
          const reader = new FileReader();
          
          reader.onload = (e) => {
            const data = e.target.result;
            const workbook = XLSX.read(data, { type: 'binary' });
            
            // Assuming first sheet and first row is header
            const firstSheetName = workbook.SheetNames[0];
            const worksheet = workbook.Sheets[firstSheetName];
            const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 });
            this.columns = jsonData[0]
            // console.log("Column Names:", this.columns);
          };
          reader.readAsBinaryString(this.uploadedFile);
          }
          else {
              alert("CSVExcel");
            }
        },async handleSubmit() {
        this.selectedColumnsArray = Object.keys(this.selectedColumns).filter(column => this.selectedColumns[column]);
        if (!this.uploadedFile) {
            alert("");
            return;
          }
  
          if (this.selectedColumnsArray.length != 2) {
            alert("2!");
            return;
          }
  
          // FormData
          const formData = new FormData();
          formData.append('file', this.uploadedFile);
          // console.log("selectedColumnsArray Names:", this.selectedColumnsArray);
          formData.append('columns', this.selectedColumnsArray);
          // console.log("formData", formData);
          // 
          this.isLoading = true;
          try {
            const response = await fetch(web_api_url+'/upload_train_data/', { // 
              method: 'POST',
              body: formData
            });
            if (response.ok) {
              alert('');
              console.log(response); // 
            } else {
              alert(':' + response.message);
            }
          } catch (error) {
            console.error(":", error);
            alert('');
          } finally {
            this.isLoading = false;  // 
            this.isServerReturn = true;
          }
      },async WatchData() {
        this.isWatchData = !this.isWatchData;
  
        if (this.isWatchData) {
          try {
            const response = await fetch(web_api_url+'/return_train_data/');
            const data = await response.json();
  
            // APIJSONDataFrame
            this.dataTable = data;
  
          } catch (error) {
            console.error("Error loading data:", error);
          }
        }
      },GoBack(){
        this.isServerReturn = false;
      },async StartTrain(){
        try{
          const response = await fetch(web_api_url+'/train/');
          const data = await response.json();
          console.log("StartTrain data:", data);
        } 
        catch (error) {
          console.error("Error loading data:", error);
        }
      },fetchData() {
        axios.get(web_api_url+'/get_train_log')
          .then(response => {
            this.Flaskdata = response.data;
            if (response.data['reload']===true){
              this.isFetching = false;
            }
            if (response.data['trained']===true){
              this.isFetching = false;
              this.isTrained = true;
            }
          });
      },toggleFetching() {
        this.StartTrain()
        this.isFetching = !this.isFetching;    
        if (this.isFetching) {
          this.fetchData();
          this.fetchInterval = setInterval(this.fetchData, 1000);
        } else {
          clearInterval(this.fetchInterval);  // , 
        }
      }
    }
  }
  </script>
  
  <style>
  h3 {
    margin-top: 20px;
  }


  /*  */
  table {
    margin-top: 10px;
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 10px;
    width: 500px;
    margin-left: auto;
    margin-right: auto;
  }
  
  th, td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  th {
    background-color: #f2f2f2;
  }
  .loading-overlay {
    position: fixed;   /*  */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.8);  /*  */
    display: flex;  /*  Flexbox */
    justify-content: center;  /*  */
    align-items: center;  /*  */
    z-index: 1000;  /*  */
  }
  
  .loading-image {
    max-width: 400px;  /*  */
    max-height: 400px;
  }
  
  .submit-button {
    background: linear-gradient(to bottom, #b0b0b0, #d9d8d8); /*  */
    color: rgb(4, 4, 4);  /*  */
    border: none;  /*  */
    padding: 10px 20px;  /*  */
    border-radius: 5px;  /*  */
    cursor: pointer;  /*  */
    transition: 0.3s;  /*  */
    margin-bottom: 20px;
  }
  
  /*  */
  .submit-button:hover {
    background: linear-gradient(to bottom, #c0c0c0, #d9d8d8);
  }
  
  /*  */
  .submit-button:active {
    background: linear-gradient(to bottom, #dedede, #b0b0b0); /*  */
  }
  </style>