<template>
  <div>
    <!--  -->
    <div v-if="isLoading" class="loading-overlay">
      <img src="/jumpingsheep.gif" alt="..." class="loading-image"/>
    </div>
    <!--  -->
    <div v-if="isServerReturn==false" class="First-Part">
      
      <h3 class="title_description">
        (csv,xlsx)
      </h3>
      <input type="file" @change="handleFileChange">
      <button class="delete-VectorDB" @click="deleteVectorDB"></button>

      <!--  -->
      <div v-if="columns.length">
        <h3 class="title_description">
          1Keywords
        </h3>
        <table v-if="columns.length">
          <thead>
            <tr>
              <th>Select</th>
              <th>Column Name</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="column in columns" :key="column">
              <td><input type="checkbox" v-model="selectedColumns[column]"></td>
              <td>{{ column }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <button class="submit-button" @click="handleSubmit" v-if="columns.length"></button>
    </div>
    
    <!--  -->
    <div v-if="isServerReturn" class="Part2">
      <!--  -->
      <button class="loadData-button" @click="WatchKeywordsData"></button>
      <button v-if="isFetching==false" class="update-vectorDB-button" @click="vectorDB_info">Keywords</button>
      <button class="delete-VectorDB" @click="deleteVectorDB"></button>
      <table v-if="isWatchData">
        <thead>
          <tr>
            <!-- DataFrame -->
            <th>Keywords</th>
            <!-- ...  ... -->
          </tr>
        </thead>
        <tbody>
          <tr v-for="row in dataTable" :key="row.id">
            <td>{{ row['Keywords'] }}</td>
            <!-- ...  ... -->
          </tr>
        </tbody>
      </table>
      <table v-if="isWatchData">
            <tr>
                <th>Keywords</th>
            </tr>
            <tbody>
                <tr>
                    <td>{{ keywordcount }}</td>
                </tr>
            </tbody>
        </table>
    </div>

    <!--  -->
    <!-- <div v-if="isPart3" class="Part3"> -->
      <!-- <table  v-if="VBTables.length">
          <thead>
              <tr>
                  <th>Select</th>
                  <th>Vector DB Table Name</th>
              </tr>
          </thead>
          <tbody>
              <tr v-for="item in VBTables"  :key="item">
                  <td><input type="checkbox" v-model="VBselectedColumns[item]"></td>
                  <td>{{ item }}</td>
              </tr>
          </tbody>
      </table> -->
      <!-- <input type="text" v-model="newVBData" placeholder="Enter new data">
      <button @click="addData">Add New Table Name</button> -->
    <!-- </div> -->
    <button class="submit-button" @click="toggleFetching" v-if="VBTables.length">VectorDB</button>
    <div v-if="isFetching | isOver" class="insertVectorDBlog">
        <h4>{{Flaskdata.data }}</h4>
    </div>
  </div>
</template>
<script>
  import Papa from 'papaparse';
  import * as XLSX from 'xlsx';
  import axios from 'axios';
  const web_api_url = 'http://127.0.0.1:5000';
  export default {
    data() {
      return {
        data: [],
        columns: [],          //  CSV 
        selectedColumns: {},  // 
        selectedColumnsArray:[],
        uploadedFile: null,  // 
        isServerReturn: false,
        isLoading: false,  // false
        isWatchData:false,
        dataTable: { attributeName: 'array'  // 
                    },
        keywordcount:0,
        VBTables: { attributeName: 'array'  // 
                    },
        // newVBData: '',
        isPart3: false,
        // VBselectedColumns: {},
        VBselectedColumnsArray: ["Hawoo_Keywords"],
        Flaskdata: {},
        isFetching:false,
        fetchInterval: null,  // ID
        isOver: false,
      }
    },
    methods: {
        handleFileChange(event) {
        this.uploadedFile = event.target.files[0];
        if (this.uploadedFile.name.includes('csv')) {
          Papa.parse(this.uploadedFile, {
            complete: (results) => {
              this.data = results.data;
              this.columns = results.data[0];
            }
          });
        }
        else if (this.uploadedFile.name.includes('xl')) {
          const reader = new FileReader();
          
          reader.onload = (e) => {
            const data = e.target.result;
            const workbook = XLSX.read(data, { type: 'binary' });
            
            // Assuming first sheet and first row is header
            const firstSheetName = workbook.SheetNames[0];
            const worksheet = workbook.Sheets[firstSheetName];
            const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 });
            this.columns = jsonData[0]
          };
          reader.readAsBinaryString(this.uploadedFile);
        }
        else {
            alert("CSVExcel");
        }
      },async handleSubmit() {
        this.selectedColumnsArray = Object.keys(this.selectedColumns).filter(column => this.selectedColumns[column]);
        if (!this.uploadedFile) {
            alert("");
            return;
          }
  
          if (this.selectedColumnsArray.length != 1) {
            alert("1!");
            return;
          }
  
          // FormData
          const formData = new FormData();
          formData.append('file', this.uploadedFile);
          formData.append('columns', this.selectedColumnsArray);
          // 
          this.isLoading = true;
          try {
            const response = await fetch(web_api_url+'/upload_keywords_data/', { // 
              method: 'POST',
              body: formData
            });
            if (response.ok) {
              alert('');
            } else {
              alert(':' + response.message);
            }
          } catch (error) {
            console.error(":", error);
            alert('');
          } finally {
            this.isLoading = false;  // 
            this.isServerReturn = true;
          }
      },async WatchKeywordsData() {
        this.isWatchData = !this.isWatchData;
  
        if (this.isWatchData) {
          try {
            const response = await fetch(web_api_url+'/return_keywords_data/');
            const data = await response.json();
  
            // APIJSONDataFrame
            this.dataTable = data.dataframe;

            this.keywordcount = data.keywords_count;
  
          } catch (error) {
            console.error("Error loading data:", error);
          }
        }
      },async deleteVectorDB(){
        if (window.confirm('')) {
          const formData = new FormData();
          formData.append('VBTable_name', this.VBselectedColumnsArray);
          this.isLoading = true;
          try{
            const response = await fetch(web_api_url+'/remove_vector_table/', { // 
              method: 'POST',
              body: formData
            });
            const data = await response.json();
            if (response.ok) {
              if (data.isRemoved==true){alert('Vector DB');}
              if (data.isRemoved==false){alert('Vector DB');}
            } else {
              alert('Vector DB:' + response.message);
            }
            console.log("Delete vector DB:", data);
          } 
          catch (error) {
            console.error("Error loading data:", error);
          } finally {
              this.isLoading = false;  // 
              this.isFetching = false;
          }
        }
      },async StartUpdateVDB(){
  
        if (this.VBselectedColumnsArray.length != 1) {
          alert("1!");
          return;
        }

        // FormData
        const formData = new FormData();
        formData.append('VBTable_name', this.VBselectedColumnsArray);
        
        this.isFetching = !this.isFetching;   
        try{
          const response = await fetch(web_api_url+'/update_vector_DB/', { // 
            method: 'POST',
            body: formData
          });
          const data = await response.json();
          console.log("Start Update vector DB:", data);
        } 
        catch (error) {
          console.error("Error loading data:", error);
        }
      },async vectorDB_info() {
        try{
          this.isWatchData = false;
          const response = await fetch(web_api_url+'/return_table_information/');
          const data = await response.json();
          this.VBTables = data["vectorDB_table_name"];
          this.isPart3 = !this.isPart3;
        } 
        catch (error) {
          console.error("Error loading data:", error);
        }
      },async handleVBSubmit() {
        this.VBselectedColumnsArray = Object.keys(this.VBselectedColumns).filter(column => this.VBselectedColumns[column]);
  
        if (this.VBselectedColumnsArray.length != 1) {
          alert("1!");
          return;
        }

        // FormData
        const formData = new FormData();
        formData.append('VBTable_name', this.VBselectedColumnsArray);
        // console.log("formData", formData);
        // 
        this.isLoading = true;
        try {
          const response = await fetch(web_api_url+'/update_vector_DB/', { // 
            method: 'POST',
            body: formData
          });
          if (response.ok) {
            alert('');
            console.log(response); // 
          } else {
            alert(':' + response.message);
          }
        } catch (error) {
          console.error(":", error);
          alert('');
        } finally {
          this.isLoading = false;  // 
          this.isServerReturn = true;
        }
      // },addData() {
      //           if (this.newVBData) {
      //               this.VBTables.push(this.newVBData);
      //               this.newVBData = '';
      //           } else {
      //               alert('Please enter some data.');
      //           }
      },fetchData() {
        axios.get(web_api_url+'/get_vector_DB_log')
          .then(response => {
            this.Flaskdata = response.data;
            if (response.data['reload']===true){
              this.isFetching = false;
            }
            if (response.data['over']===true){
              this.isFetching = false;
              this.isOver = true;
            }
          });
      },toggleFetching() {
        this.StartUpdateVDB() 
        if (this.isFetching) {
          this.fetchData();
          this.fetchInterval = setInterval(this.fetchData, 1000);
        } else {
          clearInterval(this.fetchInterval);  // , 
        }
      }
    }

  }
</script>

<style>
/*  */
  table {
    margin-top: 10px;
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 10px;
    width: 500px;
    margin-left: auto;
    margin-right: auto;
  }
  
  th, td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  th {
    background-color: #f2f2f2;
  }
  .loading-overlay {
    position: fixed;   /*  */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.8);  /*  */
    display: flex;  /*  Flexbox */
    justify-content: center;  /*  */
    align-items: center;  /*  */
    z-index: 1000;  /*  */
  }
  
  .loading-image {
    max-width: 400px;  /*  */
    max-height: 400px;
  }

  .submit-button{
    margin-top: 50px;
    margin-bottom: 50px;
  }
</style>